* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.searchWrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.content {
  flex: 1;
}

.search {
  flex: 1;
  width: 100%;
  background-color: blueviolet;
  padding: 10px;
  text-align: center;
}

.search input[type=text] {
  width: 80%;
  height: 40px;
  padding-left: 10px;
  border-radius: 10px;
}

.search input[type=submit] {
  width: 80%;
  height: 40px;
  cursor: pointer;
  background-color: #e7426b;
  color: white;
  border: 0;
  margin: 10px 0;
  font-weight: bold;
  border-radius: 10px;
}

.search h2 {
  color: white;
  padding: 20px;
}

.searchInit {
  width: 30%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  padding: 10px;
  font-size: 25px;
  background-color: #ccc;
  border-radius: 10px;
}