* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.wrapperDisplay {
    width: 100%;
}

.containerCidade {
    width: 50%;
    max-width: 500px;
    min-width: 270px;
    margin: 10% auto;
    text-align: center;
    background-color: #ccc;
    border-radius: 10px;
    -webkit-box-shadow: 14px 13px 20px 6px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 14px 13px 20px 6px rgba(0, 0, 0, 0.75);
    box-shadow: 14px 13px 20px 6px rgba(0, 0, 0, 0.75);
}

.containerCidade h2 {
    padding: 10px 0;
    color: white;
    background-color: rgb(93, 37, 146);
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}

.containerCidade p {
    border-bottom: 1px dashed black;
    padding: 10px 0;
    font-weight: bold;
}